body {
  font-family: Arial, sans-serif;
  height: 100vh;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  height: 100vh;
  
}


.search-form {
  margin-top: 20px;
  display: flex;
  justify-content: center;
}

.input-group {
  max-width: 500px;
}

.search {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 3%;
  border: none;
}

.btn {
  margin: 0.5em;
  border-radius: 3%;
  border: none;
  height: 44px;
  width: 60px;
  outline: none;
  background: #2322222b;
  color: white;
  cursor: pointer;
  transition: 0.2s ease-in-out;
}

.future-forecast {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  position: absolute;
  bottom: 10px;
  width: 100%;
  margin-bottom: 10px;
}

.weather-forecast-item {
  background: #00000079;
  color: white;
  padding: 1em;
  border-radius: 30px;
  width: 30%;
  max-width: 250px;
  margin: 0.6em;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 200px; 
}



.city-fact {
  position: absolute;
  top: 20px;
  right: 20px;
  background: #00000079;
  color: white;
  padding: 1em;
  border-radius: 10px;
  max-width: 250px;
  text-align: center;
}
